"use client"

import { io, Socket } from 'socket.io-client';

class WebRTCService {
  private socket: Socket | null = null;
  private transcriptionInterval: NodeJS.Timeout | null = null;
  private onCallStarted: (() => void) | null = null;
  private onCallEnded: (() => void) | null = null;
  private onTranscript: ((text: string) => void) | null = null;

  constructor() {
    // Initialize socket connection to your signaling server
    this.socket = io('http://localhost:3001', {
      transports: ['websocket'],
    });

    this.setupSocketListeners();
  }

  private setupSocketListeners() {
    if (!this.socket) return;

    this.socket.on('connect', () => {
      console.log('Connected to signaling server');
    });

    this.socket.on('transcript', (text: string) => {
      if (this.onTranscript) {
        this.onTranscript(text);
      }
    });
  }

  public async startCall(phoneNumber: string) {
    try {
      console.log('Simulating call to:', phoneNumber);
      
      // Simulate a successful connection
      if (this.onCallStarted) {
        this.onCallStarted();
      }

      // Simulate transcription updates
      this.transcriptionInterval = setInterval(() => {
        if (this.onTranscript) {
          const messages = [
            "Hello, how can I help you today?",
            "I understand your concern.",
            "Let me check that for you.",
            "Is there anything else you need assistance with?",
            "Thank you for your patience."
          ];
          const randomMessage = messages[Math.floor(Math.random() * messages.length)];
          this.onTranscript(randomMessage);
        }
      }, 3000);

    } catch (error) {
      console.error('Error starting call:', error);
      throw error;
    }
  }

  public endCall() {
    if (this.transcriptionInterval) {
      clearInterval(this.transcriptionInterval);
      this.transcriptionInterval = null;
    }

    if (this.onCallEnded) {
      this.onCallEnded();
    }
  }

  public setCallHandlers({
    onCallStarted,
    onCallEnded,
    onTranscript,
  }: {
    onCallStarted?: () => void;
    onCallEnded?: () => void;
    onTranscript?: (text: string) => void;
  }) {
    this.onCallStarted = onCallStarted || null;
    this.onCallEnded = onCallEnded || null;
    this.onTranscript = onTranscript || null;
  }
}

export const webRTCService = new WebRTCService();
