"use client"

import { useState, useEffect } from 'react'
import { webRTCService } from '@/lib/webrtc'

interface CallState {
  isRecording: boolean
  isPaused: boolean
  duration: number
  language: 'english' | 'hindi'
  phoneNumber: string
}

export const useCall = () => {
  const [state, setState] = useState<CallState>({
    isRecording: false,
    isPaused: false,
    duration: 0,
    language: 'english',
    phoneNumber: ''
  })

  const [transcription, setTranscription] = useState<string>('')
  const [error, setError] = useState<string | null>(null)

  useEffect(() => {
    webRTCService.setCallHandlers({
      onCallStarted: () => {
        setState(prev => ({
          ...prev,
          isRecording: true,
          isPaused: false
        }))
      },
      onCallEnded: () => {
        setState(prev => ({
          ...prev,
          isRecording: false,
          isPaused: false,
          duration: 0
        }))
        setTranscription('')
      },
      onTranscript: (text: string) => {
        setTranscription(prev => prev + text + '\n')
      }
    })
  }, [])

  useEffect(() => {
    let timer: NodeJS.Timeout

    if (state.isRecording && !state.isPaused) {
      timer = setInterval(() => {
        setState(prev => ({
          ...prev,
          duration: prev.duration + 1
        }))
      }, 1000)
    }

    return () => {
      if (timer) {
        clearInterval(timer)
      }
    }
  }, [state.isRecording, state.isPaused])

  const startCall = async (phoneNumber: string) => {
    setError(null)
    try {
      await webRTCService.startCall(phoneNumber)
      setState(prev => ({
        ...prev,
        phoneNumber,
        isRecording: true,
        isPaused: false
      }))
      setTranscription('Starting call transcription...\n')
    } catch (error) {
      console.error('Failed to start call:', error)
      setError(error instanceof Error ? error.message : 'Failed to start call')
      setState(prev => ({
        ...prev,
        isRecording: false,
        isPaused: false
      }))
    }
  }

  const pauseCall = () => {
    setState(prev => ({
      ...prev,
      isPaused: true
    }))
  }

  const resumeCall = () => {
    setState(prev => ({
      ...prev,
      isPaused: false
    }))
  }

  const stopCall = () => {
    webRTCService.endCall()
    setState(prev => ({
      ...prev,
      isRecording: false,
      isPaused: false,
      duration: 0,
      phoneNumber: ''
    }))
    setTranscription('')
  }

  const setLanguage = (language: 'english' | 'hindi') => {
    setState(prev => ({
      ...prev,
      language
    }))
  }

  const formatDuration = (seconds: number): string => {
    const minutes = Math.floor(seconds / 60)
    const remainingSeconds = seconds % 60
    return `${minutes}:${remainingSeconds.toString().padStart(2, '0')}`
  }

  return {
    ...state,
    transcription,
    error,
    startCall,
    pauseCall,
    resumeCall,
    stopCall,
    setLanguage,
    formattedDuration: formatDuration(state.duration)
  }
}
