"use client"

import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { useCall } from "@/hooks/useCall"
import { Phone, PhoneOff, Pause, Play } from "lucide-react"
import { useState } from "react"

export function CallControls() {
  const {
    isRecording,
    isPaused,
    language,
    transcription,
    formattedDuration,
    error,
    startCall,
    pauseCall,
    resumeCall,
    stopCall,
    setLanguage
  } = useCall()

  const [phoneNumber, setPhoneNumber] = useState('')

  const handleStartCall = async () => {
    if (!phoneNumber) return
    await startCall(phoneNumber)
  }

  return (
    <Card className="p-8 shadow-lg bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/60">
      <Tabs defaultValue={language} className="mb-8">
        <TabsList className="grid w-full grid-cols-2">
          <TabsTrigger 
            value="english"
            onClick={() => setLanguage("english")}
            className="data-[state=active]:bg-primary data-[state=active]:text-primary-foreground px-8 py-3"
          >
            English
          </TabsTrigger>
          <TabsTrigger 
            value="hindi"
            onClick={() => setLanguage("hindi")}
            className="data-[state=active]:bg-primary data-[state=active]:text-primary-foreground px-8 py-3"
          >
            हिंदी
          </TabsTrigger>
        </TabsList>
      </Tabs>

      <div className="text-center space-y-4">
        {error && (
          <div className="bg-destructive/10 text-destructive px-4 py-2 rounded-md text-sm">
            {error}
          </div>
        )}
        <div className="text-5xl font-bold tracking-tighter">
          {formattedDuration}
        </div>
        
        <div className="flex flex-col items-center gap-6">
          {!isRecording ? (
            <>
              <Input
                type="tel"
                placeholder="Enter phone number"
                value={phoneNumber}
                onChange={(e) => setPhoneNumber(e.target.value)}
                className="max-w-[250px] text-center h-12 text-lg border-2 focus:border-primary focus:ring-2 focus:ring-primary relative z-10 bg-background"
                autoFocus
              />
              <Button
                size="lg"
                onClick={handleStartCall}
                disabled={!phoneNumber}
                className="w-36 h-36 rounded-full bg-primary hover:bg-primary/90 transition-all hover:scale-105 shadow-lg"
              >
                <Phone className="h-10 w-10" />
              </Button>
            </>
          ) : (
            <>
              <Button
                size="lg"
                variant="outline"
                onClick={isPaused ? resumeCall : pauseCall}
                className="w-20 h-20 rounded-full border-2 hover:scale-105 transition-all"
              >
                {isPaused ? (
                  <Play className="h-8 w-8" />
                ) : (
                  <Pause className="h-8 w-8" />
                )}
              </Button>
              <Button
                size="lg"
                variant="destructive"
                onClick={stopCall}
                className="w-20 h-20 rounded-full hover:scale-105 transition-all"
              >
                <PhoneOff className="h-8 w-8" />
              </Button>
            </>
          )}
        </div>
      </div>

      <div className="mt-10 bg-muted rounded-lg p-6">
        <h3 className="text-lg font-semibold mb-4 flex items-center">
          <span className="mr-2">Live Transcription</span>
          {isRecording && !isPaused && (
            <span className="w-2 h-2 rounded-full bg-red-500 animate-pulse" />
          )}
        </h3>
        <div className="min-h-[200px] text-muted-foreground">
          {isRecording ? (
            <p className="whitespace-pre-line">{transcription}</p>
          ) : (
            <p className="italic">Start a call to begin transcription...</p>
          )}
        </div>
      </div>
    </Card>
  )
}
